a {
  text-decoration: none;
  color: #E0ECFF;
}
.work-order-box {
  border-top: 1px solid #203B60;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: normal;
  color: #FFF;
}
.work-order-box .m-work-logo {
  display: none;
}
.work-order-box .work-logo,
.work-order-box .work-info {
  margin-top: 192px;
}
.work-order-box .work-info {
  margin-left: 119px;
  width: 614px;
  height: 328px;
}
.work-order-box .work-info .work-title {
  font-style: normal;
  font-weight: 600;
  font-size: 32px;
}
.work-order-box .work-info .work-describe {
  font-size: 22px;
  color: rgba(255, 255, 255, 0.5);
  margin-top: 80px;
}
.work-order-box .work-info .go-discord {
  width: 260px;
  height: 48px;
  border-radius: 10px;
  background-color: #5577FD;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 16px;
  font-size: 18px;
  margin-top: 90px;
}
.work-order-box .work-info .go-discord img {
  margin-left: 8px;
}
@media only screen and (max-width: 1140px) {
  .work-order-box .work-info {
    margin-left: 51px;
    width: 425px;
  }
  .work-order-box .work-info .go-discord {
    margin-top: 63px;
  }
}
@media only screen and (max-width: 1040px) {
  .work-order-box .work-logo {
    display: none;
  }
  .work-order-box .m-work-logo {
    display: block;
    margin-top: 53px;
  }
  .work-order-box .m-work-logo img {
    width: 100%;
  }
  .work-order-box .work-info {
    width: 520px;
    text-align: center;
    margin: 0 auto;
    margin-top: 70px;
  }
  .work-order-box .work-info .go-discord {
    width: 520px;
    justify-content: center;
  }
}
@media only screen and (max-width: 560px) {
  .work-order-box .work-info {
    width: 339px;
    text-align: left;
    margin: 0 auto;
    margin-top: 30px;
  }
  .work-order-box .work-info .work-describe {
    margin-top: 30px;
  }
  .work-order-box .work-info .go-discord {
    width: 339px;
  }
}
